import { BoxGeometry, MeshBasicMaterial, MeshLambertMaterial, Mesh, DirectionalLight, AmbientLight, Object3D } from 'three';

// import Engine from './utils/engine';
import Engine from './utils/engine3d';
import Boxer from './Boxer';

const WIDTH = 480;
const HEIGHT = 480;

// const el = document.getElementById('canvas');
const el = document.getElementById('container');

const app = new Engine(el, {
  debug: false,
  width: WIDTH,
  height: HEIGHT
}, {
  alpha: true
});

app.camera.position.z = 40;
app.camera.position.y = 150;
app.camera.lookAt(0, 90, 0);

const ambient = new AmbientLight(0xff0000, 0.5);
app.scene.add(ambient);

const light = new DirectionalLight(0xffffff, 1);
light.position.set(5, 1, 5);
app.scene.add(light);

let size = 10;
const boxes = new Array(10).fill(1).map((v, idx) => {
  return new Boxer(app.scene, {
    x: ((Math.random() - 1) * 2) * (size * 10),
    y: idx * size,
    z: -(idx * size)
  }, Math.random() * 0xffffff);
});

let playing = false;
el.addEventListener('click', () => playing = true);

app.onRender(({ frame, width, height, debug }) => {
  if (playing) {
    for (let i = 0; i < boxes.length; i++) {
      boxes[i].update();
    }
  }
});

app.start();
