import { BoxGeometry, MeshLambertMaterial, Mesh, Object3D } from 'three';
import { MonoSynth } from 'tone';

const roundTo = (num, to = 10) => {
  return Math.round(num * to) / to;
}

const randomNote = () => {
  const arr = ['C', 'D', 'E', 'F', 'G', 'A', 'B'];
  return `${arr[Math.floor(Math.random() * arr.length)]}${Math.round(Math.random() * 2) + 3}`;
};

export default class Boxer {
  constructor(scene, position, color, size = 10) {
    this.scene = scene;

    this.position = position;
    this.color = color;
    this.size = size;

    this._createModels();

    this.synth = new MonoSynth({
      oscillator: {
        type: 'sine'
      },
      envelope: {
        attack: 0.01,
        decay: 0.1,
        sustain: 1,
        release: 0.1
      }
    }).toMaster();

    this.speed = (Math.random() / 10) + 0.01;

    this.synth.volume.value = 0;
  }

  _createModels() {
    const geometry = new BoxGeometry(this.size, this.size, this.size);
    const material = new MeshLambertMaterial({
      color: this.color,
      flatShading: true
    });
    const sphere = new Mesh(geometry, material);
    sphere.position.y = this.size / 2;
    sphere.position.x = this.size / 2;
    sphere.position.z = this.size / 2;

    this.pivot = new Object3D();
    this.pivot.position.x = this.position.x;
    this.pivot.position.y = this.position.y;
    this.pivot.position.z = this.position.z;
    this.pivot.rotation.z = Math.random();
    this.pivot.add(sphere);
    this.scene.add(this.pivot);
  }

  update() {
    this.pivot.rotation.z += this.speed;

    if (roundTo(this.pivot.rotation.z) == roundTo(Math.PI / 2)) {
      this.pivot.rotation.z = 0;
      this.pivot.position.x -= this.size;

      this.synth.triggerAttackRelease(randomNote(), '8n');
    }

    let len = this.size * 10;

    if (this.pivot.position.x < -len) {
      this.pivot.position.x = len;
    }

    this.synth.volume.value = -((40 * (Math.abs(this.pivot.position.x) / len)) + 10);
  }
}
